.wpvibes-input-wrapper input{vertical-align:middle;padding:0 8px;line-height:2;min-height:30px;box-shadow:0 2px 0 rgba(0,0,0,0.015);border-radius:2px;border:1px solid #d9d9d9;background-color:#fff;color:rgba(0,0,0,0.85)}.wpvibes-input-wrapper input:focus{border-color:#0067bb;box-shadow:0 0 0 1px #007cba;outline:2px solid transparent}.wpvibes-input-wrapper input:disabled{cursor:not-allowed;color:rgba(0,0,0,0.25);background-color:#ddd}
