.wpvibes-collapse-wrapper{width:100%;border:1px solid #d9d9d9;border-radius:2px;box-sizing:border-box;margin:0;padding:0;color:rgba(0,0,0,0.85);font-size:14px;font-variant:tabular-nums;line-height:1.5715;list-style:none}.wpvibes-collapse-wrapper .wpvibes-collapsable-border{border-bottom:1px solid #d9d9d9}.wpvibes-collapse-wrapper .wpvibes-collapsable-item{background-color:#fafafa;cursor:pointer;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center}.wpvibes-collapse-wrapper .wpvibes-collapsable-item .wpvibes-collapsable-icon{display:-webkit-flex;display:flex;margin:10px}.wpvibes-collapse-wrapper .wpvibes-collapsable-item .wpvibes-collapsable-title{margin:0 10px;position:relative;padding:12px 0;font-size:14px;color:rgba(0,0,0,0.85);line-height:1.5715;cursor:pointer}.wpvibes-collapse-wrapper .wpvibes-collapsable-item-content-wrapper{text-align:justify;display:-webkit-flex;display:flex;background-color:#fff;padding:16px;transition:all 0.3s ease}.wpvibes-collapse-wrapper .wpvibes-collapsable-item-content-wrapper .wpvibes-collapsable-item-content{width:100%}.wpvibes-collapse-wrapper .wpvibes-collapsable-item-content-wrapper.wpvibes-collapsable-close{height:0;padding-top:0;padding-bottom:0;border:none}.wpvibes-collapse-wrapper .wpvibes-collapsable-item-content-wrapper.wpvibes-collapsable-close .wpvibes-collapsable-item-content{display:none}
